import { Component, OnDestroy, OnInit } from '@angular/core';
import { faRedo, faRandom, faTrashAlt, faPlay, faVolumeUp, faVolumeDown, faVolumeOff, faVolumeMute, faStop, faForward, faBackward, faChevronUp } from '@fortawesome/free-solid-svg-icons'

import 'lazysizes';
import 'lazysizes/plugins/attrchange/ls.attrchange';

import { Channel } from '../channel';

import * as $ from 'jquery';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: [
    './player.component.css']
})
export class PlayerComponent implements OnInit, OnDestroy{
  iframeApiTag: any;

  faRedo = faRedo;
  faRandom = faRandom;
  faTrashAlt = faTrashAlt;
  faPlay = faPlay;
  faVolumeUp = faVolumeUp;
  faVolumeDown = faVolumeDown;
  faVolumeOff = faVolumeOff;
  faVolumeMute = faVolumeMute;
  faStop = faStop;
  faForward = faForward;
  faBackward = faBackward;
  faChevronUp = faChevronUp;

  player: any;
  playerSatus: number;

  playList: Channel[];
  playIdList: string[];
  loopPlaylists:boolean = false;
  shufflePlaylist:boolean = false;
  titleLight: boolean[];
  muted: boolean = false;
  sound: number = 50;
  soundState: string = "down";
  speedState: number = 1;
  footerShow: boolean = false;

  constructor() { }

  ngOnDestroy(): void {
    window['YT'] = null;
    const tag = document.getElementById("www-widgetapi-script");
    if (tag) {
      tag.remove();
    }
    if (this.iframeApiTag) {
      this.iframeApiTag.remove();
    }
  }

  ngOnInit(): void {
    if (JSON.parse(localStorage.getItem("playList"))) {
      this.pasteId();
      this.callYouTubeIframeAPI();
    }
  }

  pasteId(): void {
    this.playIdList = new Array();
    this.playList = JSON.parse(localStorage.getItem("playList"));
    for (let i = 0; i < this.playList.length; i++) {
      this.playIdList.push(this.playList[i].id.videoId);
    }
    this.titleLight = new Array(this.playList.length);
  }

  callYouTubeIframeAPI(): void {
    this.iframeApiTag = document.createElement('script');
    this.iframeApiTag.src = "https://www.youtube.com/iframe_api";
    var firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(this.iframeApiTag, firstScriptTag);
    window['onYouTubeIframeAPIReady'] = () => this.onYouTubeIframeAPIReady();
  }


  onYouTubeIframeAPIReady(): void {
    this.player = new window['YT'].Player('player', {
      videoId: this.playIdList[0],
      playerVars: {
        'controls': 0, //控制列，0:隱藏，1:顯示(默認)
        'enablejsapi': 1,
        'fs': 1,
        'playlist': this.playIdList.slice(1, this.playIdList.length).join(","),
        // 'loop': 1,
        'modestbranding': 1, //YouTube標籤，0:顯示(默認)，1:不顯示
        'playsinline': 1, //在iOS的播放器中全屏播放，0:全屏(默認)，1:內嵌
        'rel': 0, //顯示相關視頻，0:不顯示，1:顯示(默認)
        'showinfo': 0
      }
      ,
      events: {
        'onReady': this.onPlayerReady.bind(this),
        'onStateChange': this.onPlayerStateChange.bind(this),
        'onError': this.onPlayerError.bind(this)
      }
    });
  }

  onPlayerReady(event): void {
    event.target.playVideo();
    this.sound = this.player.getVolume();
    this.checkSoundState(this.sound);
  }

  onPlayerStateChange(event): void {
    this.playerSatus = event.data;
    switch (this.playerSatus) {
      case 1:
        $('#playVideo').hide();
        $('#stopVideo').show();
        break;
      case -1:
      case 2:
        $('#stopVideo').hide();
        $('#playVideo').show();
        break;
      default:
        break;
    }
  }

  onPlayerError(event): void {
    console.log(event.data);
  }

  setLoop(loopPlaylists: boolean): void {
    this.player.setLoop(loopPlaylists);
    this.loopPlaylists = loopPlaylists;
  }

  setShuffle(shufflePlaylist: boolean): void {
    this.player.setShuffle(shufflePlaylist);
    this.shufflePlaylist = shufflePlaylist;
  }

  playVideo(): void {
    this.player.playVideo();
  }

  stopVideo(): void {
    this.player.pauseVideo();
  }

  nextVideo(): void {
    this.player.nextVideo();
  }

  prevVideo(): void {
    this.player.previousVideo();
  }

  // playVideoAt(index: Number): void {
  //   this.player.playVideoAt(index);
  // }

  setVolume(): void {
    this.checkSoundState(this.sound);
    this.player.setVolume(this.sound);
  }

  checkSoundState(sound: number): void {
    this.soundState = (this.sound > 60) ? "up" : ((this.sound > 30) ? "down" : "off");
  }

  setSpeed(speed: number): void {
    this.player.setPlaybackRate(speed);
    this.speedState = speed;
  }

  togglePlayrow(index: number): void {
    this.titleLight[index] = !this.titleLight[index];
  }

  mute(): void {
    this.player.mute();
    this.muted = true;
  }

  unMute(): void {
    this.player.unMute();
    this.muted = false;
  }

  deletePlay(i: number): void {
    localStorage.clear();
    this.playList.splice(i, 1);
    localStorage.setItem("playList", JSON.stringify(this.playList));
    this.pasteId();
    this.player.loadPlaylist(
      {
        'playlist': this.playIdList.join(","),
        'suggestedQuality': 'large'
      });
  }

  toggleFooter(): void {
    this.footerShow = !this.footerShow;
  }

}
