import { Injectable } from '@angular/core';

import { Channel } from '../channel';

@Injectable({
  providedIn: 'root'
})
export class ChannelService {

  private channels: Channel[];
  private prevPageToken: string;
  private nextPageToken: string;

  constructor() { }

  setChannel(newChannels:Channel[]): void {
    this.channels = newChannels;
  }

  addChannel(newChannels:Channel[]): void {
    this.channels = this.channels.concat(newChannels);
  }

  getChannel(): Channel[] {
    return this.channels;
  }

  setPrevPageToken(prevPageToken: string): void{
    this.prevPageToken = prevPageToken;
  }

  getPrevPageToken():string{
    return this.prevPageToken;
  }

  setNextPageToken(nextPageToken: string): void{
    this.nextPageToken = nextPageToken;
  }

  getNextPageToken():string{
    return this.nextPageToken;
  }

}
