import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class YoutubeService {

  private apiKey: string = "AIzaSyDWuzGqcgh5yKi3rNBMZw9ZJO1lbWIzqeM";
  private youtubeDataUrl = "https://www.googleapis.com/youtube/v3/";
  private nowSearchUrl: string;

  constructor(public http: HttpClient) { }

  getVideosForChannel(query: string): Observable<any> {
    this.nowSearchUrl = this.pasteUrl(query);
    return this.http.get(this.nowSearchUrl).pipe(map((res) => { return res; }));
  }

  getNextVideos(nextPageToken: string): Observable<any> {
    const url = this.nowSearchUrl + "&pageToken=" + nextPageToken;
    return this.http.get(url).pipe(map((res) => { return res; }));
  }

  pasteUrl(query: string): string {
    const queryUrl = (query) ? "&q=" + query : "";
    const url = this.youtubeDataUrl + "search?key=" + this.apiKey + "&order=date&part=snippet&type=video" + queryUrl + "&maxResults=48";
    return url;
  }
}
