import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { faArrowRight, faPlayCircle, faSearch, faCompactDisc } from '@fortawesome/free-solid-svg-icons';

import { YoutubeService } from './service/youtube.service';
import { ChannelService } from './service/channel.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: [
    './app.component.css'
  ]
})
export class AppComponent {
  title = 'GoogleYoutubeDataApi';

  faArrowRight = faArrowRight;
  faPlayCircle = faPlayCircle;
  faSearch = faSearch;
  faCompactDisc = faCompactDisc;
  
  navbarShow: boolean;
  asideShow: boolean;
  asideDeg: number = 0;
  searchLoading: boolean;

  constructor(private router: Router, private youTubeService: YoutubeService, private channelService: ChannelService) { }

  ngOnInit() {
  }

  search(): void {
    this.searchLoading = true;
    const doc = <HTMLInputElement>document.getElementById('query');
    const query = doc.value;
    this.youTubeService.getVideosForChannel(query).subscribe(result => {
      console.log(result);
      this.channelService.setChannel(result.items);
      if (result.prevPageToken) {
        this.channelService.setPrevPageToken(result.prevPageToken);
      } else {
        this.channelService.setPrevPageToken(null);
      }
      this.channelService.setNextPageToken(result.nextPageToken);
      this.searchLoading = false;
      this.router.navigate(['Search']);
      this.closeNavbar();
    })
  }

  goPlayer(): void {
    this.router.navigate(['Player']);
    this.closeNavbar();
  }

  toggleNavbar(): void {
    this.navbarShow = !this.navbarShow;
  }

  toggleAside(): void {
    this.asideShow = !this.asideShow;
    this.toggleAsideDeg();
  }

  toggleAsideDeg(): void {
    if (this.asideShow) {
      this.asideDeg = 180;
    } else {
      this.asideDeg = 0;
    }
  }

  closeNavbar(): void {
    this.navbarShow = false;
    this.asideShow = false;
    this.toggleAsideDeg();
  }

}
